.footer {
    background: radial-gradient(circle, var(--color-s), var(--color-e));
    animation:radial-transition 5s infinite alternate;
    animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
    color: var(--color-light);
    padding: 2rem 2rem;
    display: flex;
}
:root {
    --color-s: #201334; 
    --color-e: #161B22; 
}

.footer .left {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.footer .left h2 {
    font-size: 1.75rem;
}

.footer .left .social {
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
}

.footer .left .social a img {
    width: 20px;
    filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7056%) hue-rotate(230deg) brightness(106%) contrast(106%);
}

.footer .left .contacts {
    display: flex;
    gap: 4rem;
}

.footer .left .contacts .contact {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}


.footer .left .contacts .contact span {
    display: flex;
    gap: 0.5rem;
}

.footer .left .contacts .contact span p a{
    color: var(--color-light);
}

.footer .left .contacts .contact img {
    width: 20px;
}

.footer .left .copyright p {
    font-size: 1rem;
}

@media only screen and (max-width:780px) { 
    .footer {
        padding: 2rem 1.5rem;
    }

    .footer .left .contacts {
        flex-direction: column;
        gap: 2rem;
    }
}
